.promo-block {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 112px;
  margin-bottom: 145px;

  @include vp-1023 {
    margin-top: 100px;
    margin-bottom: 93px;
  }

  @include vp-767 {
    margin-top: 65px;
    margin-bottom: 85px;
  }
}

.promo-block__container {
  width: 100%;
  max-width: 1366px;
  display: grid;
  grid-template-columns: 50% 50%;
  padding: 0 7.5%;
  align-items: center;

  @include vp-1023 {
    padding: 0 45px;
    align-items: start;
  }

  @include vp-767 {
    grid-template-columns: 100%;
    grid-auto-rows: auto;
    padding: 0 15px;
  }
}

.promo-block__text {
  display: flex;
  flex-direction: column;
  max-width: 512px;
  margin-right: 20px;
  height: 100%;
  margin-top: 112px;

  @include vp-1023 {
    margin-top: 0;
    padding-top: 0;
    margin-right: 14px;
  }

  @include vp-767 {
    margin-right: 0;
    margin-bottom: 43px;
  }
}

.promo-block h2 {
  @include montserrat(600, 28px, 42px, $color-navy);
  margin-bottom: 24px;

  @include vp-1023 {
    @include montserrat(600, 26px, 36px, $color-navy);
    margin-top: -10px;
    margin-bottom: 17px;
  }

  @include vp-767 {
    @include montserrat(600, 24px, 34px, $color-navy);
  }
}

.promo-block p {
  @include montserrat(400, 16px, 24px, $color-navy);
  margin: 0;
}

.promo-block__img {
  width: auto;
  max-width: 560px;
  height: 301px;
  margin-left: 20px;
  background-color: $color-deep-sky-blue;
  background-image: image-set(
    "../img/promo/promo-desk.webp" 1x,
    "../img/promo/promo-desk@2x.webp" 2x
  );

  @include back-set;

  @include vp-1023 {
    width: auto;
    max-width: 324px;
    height: 174px;
    margin-left: 12px;
    background-color: $color-deep-sky-blue;
    background-image: image-set(
      "../img/promo/promo-tab.webp" 1x,
      "../img/promo/promo-tab@2x.webp" 2x
    );

    @include back-set;
  }

  @include vp-767 {
    display: flex;
    width: 100%;
    max-width: 100%;
    height: 388px;
    margin: 0 auto;
    min-height: 100%;
    background-color: $color-deep-sky-blue;
    background-image: image-set(
      "../img/promo/promo-mob.webp" 1x,
      "../img/promo/promo-mob@2x.webp" 2x
    );
    background-position: top;

    @include back-set;
  }

  @include vp-569 {
    height: 297px;
  }

  @include vp-320 {
    margin-top: 0;
    width: 290px;
    height: 156px;
  }
}
